 import React from 'react'
import Card from './Card'
 
 function Services() {
   return (
     <>
       <div className="my-5">
          <h1 className="text-center">Our Services</h1>
       </div>
       <div className="container-fluid mb-5">
      <div className="row">
        <div className="col-10 mx-auto">
            <div className="row gy-4">
              <Card number="1" imgName="coding"/>
              <Card number="2" imgName="program"/>
              <Card number="3" imgName="data"/>
              <Card number="4" imgName="mobile"/>
              <Card number="5" imgName="laptop"/>
              <Card number="6" imgName="technologies"/>
            </div>
        </div>
      </div>
    </div> 
     </>
   )
 }
 
 export default Services
 