import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import '../node_modules/bootstrap/dist/js/bootstrap.bundle'

import Home from './Home'
import Services from './Services'
import Contact from './Contact'
import About from './About';
import Navbar from './Navbar'

import './App.css';
import Footer from './Footer';

const App = ()=>{
  return (
    <>
    <Navbar />
    <Switch> {/* we use switch to get only that page which got clicked */}
      <Route exact path="/" component={Home} />             {/* Route are like options  */}
      <Route exact path="/about" component={About} />       {/* Route are like options  */}
      <Route exact path="/services" component={Services} /> {/* Route are like options  */}
      <Route exact path="/contact" component={Contact} />   {/* Route are like options  */}
      <Redirect to="/" />
    </Switch>
    <Footer />
    </>
  )
}
export default App;
