import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'
import './index.css'
import {BrowserRouter} from 'react-router-dom'

ReactDOM.render(
  
  <React.Fragment> {/* React Fragment to get element directly inside div root*/}
    <BrowserRouter> {/* Borwser Router as we want routing in out app */}
      <App />  {/* Component that Contain routes */}
    </BrowserRouter>
  </React.Fragment>
  
  ,document.getElementById('root'))
