import React from 'react'
import { Redirect } from 'react-router-dom'

function Contact() {

  const [data,setData] = React.useState({
    fname:"",
    lname:"",
    email:"",
    message:""
  })

  const inputEvent = (event)=>{
    const {name,value} = event.target
    setData((preVal)=>{
        return {
          ...preVal,
          [name]:value
        }
    })
  }
  const submitChange = (event)=>{
    event.preventDefault()
  }

  return (
    <>
      <div className="my-5">
        <h1 className="text-center">Contact Us</h1>
      </div>
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
          <form onSubmit={submitChange}>
  <div class="form-group mb-3 ">
    <label for="exampleFormControlInput1">First Name</label>
    <input name="fname" value={data.fname} onChange={inputEvent} type="text" class="form-control" />
  </div>
  <div class="form-group mb-3 ">
    <label for="exampleFormControlInput1">Last Name</label>
    <input name="lname" value={data.lname} onChange={inputEvent} type="text" class="form-control" />
  </div>
  <div class="form-group mb-3 ">
    <label for="exampleFormControlInput1">Email address</label>
    <input name="email" value={data.email} onChange={inputEvent} type="email" class="form-control" />
  </div>
  <div class="form-group mb-3 ">
    <label for="exampleFormControlTextarea1">Message</label>
    <textarea name="message" value={data.message} onChange={inputEvent} class="form-control" id="exampleFormControlTextarea1" rows="5"></textarea>
  </div>
  <input className="btn btn-primary" type="submit" />
</form>
          </div>
        </div>
      </div>
    </>
  )
}

export default Contact
